import Vue from 'vue'
import VueRouter from 'vue-router'
import MyProductList from './components/productlist/MyProductList.vue'
import MyLandingPage from './components/landingpage/MyLandingPage.vue'
import MyProductDetail from './components/product-detail/MyProductDetail.vue' 
import MyCart from './components/cart/MyCart.vue'
Vue.use(VueRouter)

const routes = [
    {path: '/', component: MyLandingPage, name: 'MyLandingPage'},
    {path: '/products', component: MyProductList, name: 'ProductList'},
    {path: '/product/:id', component: MyProductDetail, name: 'ProductDetail'},
    {path: '/cart', component: MyCart, name: 'Cart'}                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                   
]

const router = new VueRouter({
    mode: 'history',
    base: '/FrontEnd-Ecommerce-Website/',
    routes
})

export default router;